export default (sequelize, Sequelize) => {
  const StudyFee = sequelize.define("studyFee", {
    type: {
      type: Sequelize.STRING,
    },
    name_th: {
      type: Sequelize.STRING,
    },
    name_en: {
      type: Sequelize.STRING,
    },
    pricePerUnit: {
      type: Sequelize.STRING,
    },
    allCost: {
      type: Sequelize.STRING,
    },
  });
  return StudyFee;
};
